.QuizList__QuizList--1Dlta {
	display: flex;
	justify-content: center;
	padding-top: 100px;
	flex-grow: 1;
	width: 100%;
	background: -webkit-linear-gradient(left, #fd8355 0%, #f0576c 37%, #f79cbd 100%);
	background: linear-gradient(90deg, #fd8355 0%, #f0576c 37%, #f79cbd 100%);
}

.QuizList__QuizList--1Dlta h1 {
	color: #fff;
}

.QuizList__QuizList--1Dlta ul {
	list-style: none;
	margin: 0;
	padding: 0;
}

.QuizList__QuizList--1Dlta li {
	margin-bottom: 10px;
}

.QuizList__QuizList--1Dlta a {
	text-decoration: none;
	color: #fff;
}

.QuizList__QuizList--1Dlta a:hover {
	color: #363f54;
}