.Select__Select--zn-IS {
	margin-bottom: 15px;
}

.Select__Select--zn-IS label {
	margin: 0 0 3px 0;
	padding: 0;
	font-weight: bold;
	display: block;
}

.Select__Select--zn-IS select {
	display: block;
	box-sizing: border-box;
	border: 1px solid #bebebe;
	margin: 0 0 5px;
	height: 29px;
	width: 100%;
	outline: none;
	-webkit-transition: all .3s ease-in-out;
	transition: all .3s ease-in-out;
}