.Button__Button--ajevi {
	display: inline-block;
	padding: 10px 20px;
	border-radius: 4px;
	border: 1px solid #fff;
	color: #000;
	margin-right: 15px;
	text-transform: uppercase;
	font-weight: bold;
	font-size: 12px;
}

.Button__Button--ajevi:focus {
	outline: none;
}

.Button__Button--ajevi:active {
	box-shadow: inset 2px 2px 1px rgba(0, 0, 0, .3);		
}

.Button__Button--ajevi:disabled {
	background: #ccc;
	color: #000;
	cursor: not-allowed;
}

.Button__error--3i2F2 {
	background: rgba(240, 87, 108, 1);
}

.Button__success--1wqIA {
	background: rgba(161, 240, 69, 1);
}

.Button__primery--wmZRP {
	background: #2884f6;
	color: #fff;
}